@import url('https://fonts.googleapis.com/css?family=Roboto');
$base-font: 'Microsoft YaHei', 'Roboto', sans-serif;
$black_honk: #060b24;
$blue_honk: #01d6fe;

@mixin flex() {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

::selection {
  background: #fff;
  color: $blue_honk;
}

body {
  font-family: $base-font;
  border-radius: 5px;
}
.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}
.btn {
  display: inline-block;
  padding: 10px 20px;
  margin: 5px 10px;
  background: $blue_honk;
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  transition: background 0.5s;
}
.btn:hover {
  background: $black_honk;
}
.red {
  color: $blue_honk;
}
header {
  background: $black_honk;
  padding: 20px 0;
  color: #fff;
  border-bottom: 5px solid $blue_honk;

  .header {
    @include flex();

    .title {
      font-size: 24px;
      font-weight: bold;
      text-transform: uppercase;
    }
  }
}
main {
  min-height: calc(100vh - 249px);
  margin-top: 20px;
  padding: 150px 0 20px;
  background: url(/image/bg_main.png) no-repeat center top;

  .btns {
    text-align: center;
  }

  #form {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
    margin: 0 -10px;

    .form_item {
      margin: 0 10px 15px;
    }
    label {
      display: block;
      margin-bottom: 10px;
      cursor: pointer;
    }
    input {
      width: 100%;
      max-width: 300px;
      padding: 10px 20px;
      border: 1px solid #808080;
      border-radius: 5px;
    }
    .btn {
      width: 100%;
      max-width: 300px;
      margin: 0;
    }
  }

  .result_box {
    text-align: left;

    .top{
      @include flex();
      margin-bottom: 20px;
    }
    #result {
      padding: 10px;
      border-top: 1px solid $black_honk;
      b {
        color: $blue_honk;
      }
    }
  }
}
footer {
  padding: 20px 0;
  background: $black_honk;
  color: #fff;
  .footer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}